<template>
  <div class="profile-container">
    <!-- Profile Component -->
    <div class="profile-component" @click="toggleMenu">
      <img :src="profileImage" alt="Profile Picture" class="profile-image" />
      <div class="profile-info">
        <h3>{{ name }}</h3>
        <p>{{ username }}</p>
      </div>

      <!-- Menu Dropdown -->
      <div 
        class="menu-dropdown" 
        :class="{ 'active': isMenuVisible }"
      >
        <AccountProfile 
          :profileImage="profileImage"
          :name="name" 
          :friendsCount="friendsCount"
        />
        <p>Setting</p>
        <button class="logout-btn" @click="handleLogout">Logout {{ username }}</button>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, onMounted } from 'vue';
import { useStore } from 'vuex';
import defaultImagePath from '@/assets/male.png';
import AccountProfile from './AccountProfile.vue';

const store = useStore();
const defaultImage = defaultImagePath;
const profileImage = ref(defaultImage);
const name = ref('Sunny Tseng');
const username = ref('__SunnyTseng__');
const friendsCount = ref(0);
const isMenuVisible = ref(false);

const toggleMenu = () => {
  isMenuVisible.value = !isMenuVisible.value;
};

const handleClickOutside = (event: Event) => {
  const profileComponent = document.querySelector('.profile-component');
  if (profileComponent && !profileComponent.contains(event.target as Node)) {
    isMenuVisible.value = false;
  }
};

onMounted(() => {
  document.addEventListener('click', handleClickOutside);
});

const handleLogout = () => {
  store.dispatch("auth/logout");
};

// 模擬 API 請求
const fetchProfileImage = async () => {
  try {
    const response = await fetch('https://api.example.com/user/profilePict');
    if (response.ok) {
      const data = await response.json();
      if (data && data.image) {
        profileImage.value = data.image;
      }
    } else {
      console.error('Failed to fetch profile image:', response.status);
    }
  } catch (error) {
    console.error('Error fetching profile image:', error);
  }
};

onMounted(() => {
  fetchProfileImage();
});

</script>

<style scoped>
/* 外層容器 */
.profile-container {
  position: relative;
  display: inline-block;
  margin-top: 30px;
}

/* Profile Component */
.profile-component {
  display: flex;
  align-items: center;
  gap: 10px;
  cursor: pointer;
  position: relative;
}

.profile-image {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover;
}

.profile-info h3 {
  margin: 0;
  font-size: 1rem;
}

.profile-info p {
  margin: 0;
  color: gray;
}

/* 選單樣式 */
.menu-dropdown {
  position: absolute;
  bottom: 100%;
  left: 0;
  background: black;
  color: white;
  padding: 10px;
  border-radius: 5px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  width: 200px;
  z-index: 10;
  opacity: 0;
  transform: translateY(10px);
  pointer-events: none; /* 防止被點擊 */
  transition: opacity 0.3s ease, transform 0.3s ease;
}

.menu-dropdown.active {
  opacity: 1; /* 顯示選單 */
  transform: translateY(0); /* 還原位置 */
  pointer-events: auto; /* 啟用點擊 */
}

.profile-component:hover .menu-dropdown {
  opacity: 1;
  transform: translateY(0);
  pointer-events: auto;
}

.menu-dropdown p {
  margin: 0;
  padding: 10px;
  cursor: pointer;
}

.menu-dropdown p:hover {
  background: #333;
  border-radius: 5px;
}

/* 過渡效果 */
.menu-slide-enter-active,
.menu-slide-leave-active {
  transition: all 0.3s ease; /* 平滑過渡效果 */
}

.menu-slide-enter-from {
  opacity: 0; /* 初始透明度 */
  transform: translateY(10px); /* 初始位置稍微下移 */
}

.menu-slide-leave-to {
  opacity: 0; /* 離開時透明 */
  transform: translateY(10px); /* 離開時下移 */
}

.logout-btn {
  padding: 10px;
  font-size: 1rem;
  font-weight: bold;
  color: #fff;
  background: linear-gradient(135deg, #ff416c, #ff4b2b); /* 漸層背景 */
  border: none;
  border-radius: 7px; /* 圓角按鈕 */
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.2); /* 添加陰影效果 */
  cursor: pointer;
  transition: all 0.3s ease; /* 添加過渡效果 */
}

.logout-btn:hover {
  background: linear-gradient(135deg, #ff4b2b, #ff416c); /* 懸停時反向漸層 */
  transform: scale(1.05); /* 懸停時放大效果 */
}

.logout-btn:active {
  transform: scale(0.95); /* 點擊時縮小效果 */
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2); /* 點擊時降低陰影 */
}

/* 響應式樣式 */
@media (max-width: 768px) {
  .profile-info,

  .more-options {
    display: none;
  }

  .profile-info {
    display: none;
  }

  .logout-btn {
    font-size: 0.7rem;

  }
}
</style>
